import react, { useState, useEffect } from "react";

const InstaBar = () => {
  const navItems = [
    {
      id: "1",
      name: "Home",
      icon: "fa fa-home"
    },
    {
      id: "2",
      name: "messenger",
      icon: "fa-brands fa-facebook-messenger"
    },
    {
      id: "3",
      name: "Add",
      icon: "fa-solid fa-square-plus"
    },
    {
      id: "4",
      name: "Safari",
      icon: "fa-brands fa-safari"
    },
    {
      id: "5",
      name: "Likes",
      icon: "fa-solid fa-heart"
    },
    {
      id: "6",
      name: "Profile",
      icon: "fa fa-user"
    }
  ];

  return (
    <>
      <div className="insta-bar">
        <div className="left-side-nav">Instagram</div>
        <div className="center-nav">
          <input
            type="search"
            placeholder="Search"
            className="search-bar"
            name="search"
          />
        </div>
        <div className="right-side-nav">
          {navItems.map((item) => (
            <i className={item.icon + " nav-icon"} aria-hidden="true"></i>
          ))}
        </div>
      </div>
    </>
  );
};

export default InstaBar;
