import "./styles.css";
import { useEffect } from "react";
import Home from "./DashboardComponents/Home";
import Profile from "./DashboardComponents/Profile";
import Settings from "./DashboardComponents/Settings";

const Dashboard = () => {
  useEffect(() => {
    makeHidden();
    document.querySelector(".DashboardHome").style.display = "block";
  }, []);

  function makeHidden() {
    document.querySelectorAll(".showComponent").forEach(function (e) {
      e.style.display = "none";
    });
  }
  const changeComponent = (e) => {
    let component = e.target.innerText;
    makeHidden();
    if (component === "Home") {
      document.querySelector(".DashboardHome").style.display = "block";
    }
    if (component === "Profile") {
      document.querySelector(".DashboardProfile").style.display = "block";
    }
    if (component === "Settings") {
      document.querySelector(".DashboardSettings").style.display = "block";
    }
  };

  return (
    <>
      <div className="row">
        <div className="col-md-2 sidebar">
          <ul>
            <li onClick={(e) => changeComponent(e)}>Home</li>
            <li onClick={(e) => changeComponent(e)}>Profile</li>
            <li onClick={(e) => changeComponent(e)}>Settings</li>
          </ul>
        </div>
        <div className="col-md-9">
          <div className="showComponent DashboardHome">
            <Home />
          </div>
          <div className="showComponent DashboardProfile">
            <Profile />
          </div>
          <div className="showComponent DashboardSettings">
            <Settings />
          </div>
        </div>
      </div>
    </>
  );
};

export default Dashboard;
