import "./styles.css";
import { useState, useEffect } from "react";
import Dashboard from "./Dashboard";
import Contact from "./Contact";
import Blog from "./Blog";
import axios from "axios";
import ImgBox from "./HomeComponents/ImgBox";

const App = () => {
  const navItems = ["Home", "Dashboard", "Blog", "Contact"];
  const footer = ["Facebook", "Instagram", "Blog", "Privacy Policy"];
  const Site = "Test.com";
  const [imageArr, setImageArr] = useState([]);
  const [currentPage, setCurrentPage] = useState("Home");

  const change = (e) => {
    console.log(e.target.innerText);
  };

  useEffect(() => {
    if (localStorage.getItem("imagesArr") !== null) {
      let img = localStorage.getItem("imagesArr");
      let decodeArr = JSON.parse(img);
      setImageArr(decodeArr);
    } else {
      axios.get("https://picsum.photos/v2/list").then((response) => {
        setImageArr(response.data);
        let decodeArr = response.data;
        decodeArr.forEach(function (item, index) {
          decodeArr[index].likes = 0;
        });
        let encodedArr = JSON.stringify(decodeArr);
        localStorage.setItem("imagesArr", encodedArr);
      });
    }
  }, [0]);
  const changePage = (value) => {
    console.log(value);
    setCurrentPage(value);
    document.querySelectorAll(".show").forEach(function (e) {
      e.style.display = "none";
    });
    document.querySelector("." + value).style.display = "block";
  };

  return (
    <>
      {/* <div className="navBar">
        <ul>
          {navItems.map((item, index) => (
            <li key={index} onClick={(e) => changePage(e.target.innerText)}>
              {item}
            </li>
          ))}
        </ul>
      </div> */}
      <div className="">
        <div className="show Home">
          <div className="post-flex">
            <div className="col-md-3" style={{ marginTop: "5px" }}></div>
            <div className="col-md-5">
              {imageArr.map((item, index) => (
                <ImgBox item={item} key={item.id} index={index} />
              ))}
            </div>
            <div className="col-md-4" style={{ marginTop: "5px" }}></div>
          </div>
        </div>
        <div className="show Dashboard" style={{ display: "none" }}>
          <Dashboard />
        </div>
        <div className="show Blog" style={{ display: "none" }}>
          <Blog />
        </div>
        <div className="show Contact" style={{ display: "none" }}>
          <Contact />
        </div>
      </div>
      <div className="footer">
        <div style={{ display: "flex" }}>
          <ul>
            <li>{Site}</li>
          </ul>
          <ul>
            {footer.map((item, index) => (
              <li key={index}>{item}</li>
            ))}
          </ul>
        </div>
      </div>
    </>
  );
};

export default App;
