import "../styles.css";

const Profile = () => {
  const showAlert = () => {
    let confirmation = confirm("Are you sure? You want to leave!");
    if (confirmation) {
      alert("Stay here!");
    }
  };

  return (
    <>
      <div>
        <div className="profileTitle">
          <strong>Name:</strong>Love
        </div>
        <div className="profileTitle">
          <strong>Email:</strong>test@here.only
        </div>
        <div className="profileTitle">
          <strong>Languages:</strong>Php,React,vue
        </div>
        <div className="profileTitle">
          <strong>Experience:</strong>1 Year
        </div>
        <div className="clear" />
        <div>
          <h3>Social Handles</h3>
        </div>
        <div className="row">
          <div
            onClick={() => showAlert()}
            style={{ margin: "2px" }}
            className="social-links"
          >
            <i className="fa-brands fa-facebook"></i>Facebook
          </div>
          <div
            onClick={() => showAlert()}
            style={{ margin: "2px" }}
            className="social-links"
          >
            <i className="fa-brands fa-instagram"></i>Instagram
          </div>
          <div
            onClick={() => showAlert()}
            style={{ margin: "2px" }}
            className="social-links"
          >
            <i className="fa-brands fa-pinterest"></i>Pinterest
          </div>
        </div>
      </div>
    </>
  );
};

export default Profile;
