import "../styles.css";

const Settings = () => {
  const sidebarStyle = (e, item) => {
    var sidebarColor = e.target.value;
    if (item === "sidebar") {
      document.querySelector(".sidebar").style.background = sidebarColor;
    }
    if (item === "item") {
      document.querySelector(".sidebar>ul>li").style.color = sidebarColor;
    }
  };
  return (
    <>
      <div className="form-group">
        Sidebar Color:{" "}
        <input
          type="color"
          name="sidebar_color"
          onChange={(e) => sidebarStyle(e, "sidebar")}
        />
      </div>
      <div className="form-group">
        Sidebar Item Color:{" "}
        <input
          type="color"
          name="sidebar_color"
          onChange={(e) => sidebarStyle(e, "item")}
        />
      </div>
    </>
  );
};

export default Settings;
